import { changelog } from '@/changelog';

export default defineComponent({
  name: 'Changelog',
  components: {
    Layout,
    HeroApp,
  },
  data() {
    return {
      appName: APP_NAME,
      appVersion: APP_VERSION,
      md: changelog,
    };
  },
});
